.table-hover.users-table>tbody>tr:hover{background-color:#FFFFCC;cursor:pointer;}
.table-hover.users-table>tbody>tr>td:first-child{
  width: 21px;
}

.hov-icon:before{
  display: none;
}

.table-hover>tbody>tr:hover>td:first-child i.hov-icon:before{
  display: inline-block;
}

.control-sidebar-dark{
  .tab-pane{
    input[type="number"], input[type="text"]{
      background-color: transparent;
      border: none;
      border-bottom: 1px solid gray;
      width: 60px;
      outline: 0;
    }
    input.email{
      float: none;
      display: block;
      width: 100%;
    }
    &:focus{
      outline: 0;
    }
  }
}

.dataTables_wrapper{
  .row{
    margin-left: 0;
    margin-right: 0;
  }
}

.answer-question-image{
  width: 100%;
  height: auto;
}

.question-date{
  font-size: 18px;
  fotn-weight: bold;
}

.question-ip{
  color: gray;
}

.question-body{
  font-size: 16px;
  margin-top: 10px;
}

.textarea-html5{
  width: 100%;
  height: 600px;
  border: 1px solid #dddddd;
  padding: 10px;
}

.text-color-pending{
  color: orange;
}

.text-color-edited{
  color: blue;
}

.text-color-published{
  color: #47EB13;
}

.text-color-archived{
  color: darkgray;
}

.article-details span{
  margin-right: 25px;
}

.article-action-btn{
  display: inline-block;
  margin-right: 15px;
}

span.article-current-status{
  font-size: 20px;
  line-height: 2.3;
}

tr.periods {
  cursor: pointer;
}

.rating-stars{
  i{
    color: #DAA520;
    display: inline-block;
    //margin-right: 5px;
  }
}

.italic{
  font-style: italic;
}

.bigger-checkbox input{
  margin-top: 0;
  width: 20px;
  height: 20px;
}
.phrase-add{
  margin-top: 20px;
}

#timetable .box-footer input{
  width: 40px;
}

.btn-timetable{
  padding: 0 12px;
  line-height: 1.72;
  vertical-align: top;
  margin-left: 10px;
}

.time-table{
  width: 100%;
  margin-bottom: 20px;
}
.time-table tr th{
  height: 30px;
  text-align: center;
  width: 14.285714286%
}
.time-table tr td .empty,
.time-table tr td .busy{
  padding: 0;
  margin: 0;
  height: 10px;
  display: block;
  float: left;
}
.time-table tr td .empty{
  background-color: transparent;
}
.time-table tr td .busy{
  background-color: green;
  cursor: pointer;
}
.time-table tr td .busy:hover{
  background-color: #00b400;
}
.time-table tr td{
  position:relative;
  border-right: 1px solid black;
}
.time-table tr td:first-child{
  border-left: 1px solid black;
}
.time-table tr td .zero-hour{
  position: absolute;
  bottom: -20px;
  left: -10px;
}
.time-table tr td .right-stick{
  right: -10px;
  position: absolute;
  bottom: -20px;
}
.time-table tr td .slot-tooltip{
  position: absolute;
  padding: 1px 5px;
  color: white;
  background-color: black;
  top: -30px;
  font-size: 14px;
  display: none;
  width: 85px;
  border-radius: 5px;
}
.ajax-notification{
  top: 58px;
  right: 20px;
  position: fixed;
  z-index: 9999;
  background-color: white;
  padding: 10px 29px;
  border: 1px solid #E1E1E1;
  border-radius: 10px;
}
.ajax-notification .new-messages{
  font-weight: bold;
}
.inline-block{
  display: inline-block;
}
.push-left{
  float: left;
}
.push-right{
  float: right;
}